
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'

import {mapState, mapActions} from 'vuex'

export default {
directives: {
TransferDom,
},
components: {
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet,
Datetime
},
data() {
return {
menus: [
{
title: '报警管理',
value: 'index1',
icon:require('../../assets/images/index3/bj_black@2x.png'),
activeIcon:require('../../assets/images/index3/bj_red@2x.png')
}, {
title: '操作记录',
value: 'index2',
icon:require('../../assets/images/index3/czjl_black@2x.png'),
activeIcon:require('../../assets/images/index3/czjl_red@2x.png')
}, {
title: '电站设备',
value: 'index3',
icon:require('../../assets/images/index3/dzsb_black@2x.png'),
activeIcon:require('../../assets/images/index3/dzsb_red@2x.png')
}, {
title: '运维设备',
value: 'index4',
icon:require('../../assets/images/index3/ywsb_black@2x.png'),
activeIcon:require('../../assets/images/index3/ywsb_red@2x.png')
}, {
title: '电站',
value: 'index5',
icon:require('../../assets/images/index3/dz_black@2x.png'),
activeIcon:require('../../assets/images/index3/dz_red@2x.png')
}
],
options: [{
value: '选项1',
label: '黄金糕'
}, {
value: '选项2',
label: '双皮奶'
}, {
value: '选项3',
label: '蚵仔煎'
}, {
value: '选项4',
label: '龙须面'
}, {
value: '选项5',
label: '北京烤鸭'
}],
tabActive:0,
value: '',
rightOptions: {
value: ''
},
entryUrl: document.location.href,
showMenu: false,
drawerVisibility: false,
showMode: 'push',
showModeValue: 'push',
showPlacement: 'left',
showPlacementValue: 'left'
}
},
methods: {
openSearchPage(){
this.$router.push('searchBaoJing');
},
tabPage(index) {
this.tabActive=index;
},
showDrawer() {
this.drawerVisibility = !this.drawerVisibility;
},
onShowModeChange(val) {
/** hide drawer before changing showMode **/
this.drawerVisibility = false
setTimeout(one => {
this.showModeValue = val
}, 400)
},
onPlacementChange(val) {
/** hide drawer before changing position **/
this.drawerVisibility = false
setTimeout(one => {
this.showPlacementValue = val
}, 400)
},
onClickMore() {
this.showMenu = true
},
changeLocale(locale) {
this.$i18n.set(locale)
this.$locale.set(locale)
},
...mapActions([
'updateDemoPosition'
])
},
mounted() {
this.handler = () => {
if (this.path === '/demo') {
this.box = document.querySelector('#demo_list_box')
this.updateDemoPosition(this.box.scrollTop)
}
}
},
beforeDestroy() {
this.box && this.box.removeEventListener('scroll', this.handler, false)
},
watch: {},
computed: {
...mapState({
route: state => state.route,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
}),
isShowBar() {
if (this.entryUrl.indexOf('hide-tab-bar') > -1) {
return false
}
return true
},
isShowNav() {
if (this.entryUrl.indexOf('hide-nav') > -1) {
return false
}
return true
},
isTabbarDemo() {
return /tabbar/.test(this.route.path)
}
},

}
