
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import encrypt from '../../util/jsencrypt'

export default {
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tabbar,
TabbarItem,
Group,
Cell,
XInput,
XButton
},
data() {
return {
username: '李海涛',
nickname: 'Dolphin',
telephone: '17630378060',
email:'1340706176@qq.com',
password: '123321',
code: '',
codeText: '获取验证码',
sending: false,
menus: {
menu1: 'Take Photo',
menu2: 'Choose from photos'
},
showMenus: false,
registerType: 0
}
},
methods: {
consoleIndex() {

},
sendCode() {
if (this.sending) {
return false;
}
if (!this.Tools.RegExp.Phone.test(this.telephone)) {
this.$vux.toast.text('请输入正确的手机号码');
return false;
}
this.sending = true;
let n = 120;
this.codeText = '再次获取(' + n + 's)';
let timer = setInterval(() => {
if (n === 0) {
this.sending = false;
this.codeText = '获取验证码';
clearInterval(timer);
} else {
this.codeText = '再次获取(' + n + 's)';
n--;
}
}, 1000);
this.Tools.ajax({
method: '/sendregistermsg',
data: {
"telephone": this.telephone
}
}).then(res => {
console.log(res)
switch (res.status) {
case '0':

this.$vux.toast.text('验证码已发送请注意查收');
break;
case '2':
this.$vux.toast.text('2分钟内不重复发送短信');
break;
default:

this.$vux.toast.text('发送失败，请稍后重试！');
break;
}
});
},
register() {
if (!this.username) {
this.$vux.toast.text('请输入用户名');
return false;
}
if (!this.Tools.RegExp.Phone.test(this.telephone)) {
this.$vux.toast.text('请输入正确的手机号码');
return false;
}
if (!this.Tools.RegExp.password.test(this.password)) {
this.$vux.toast.text('请输入正确的密码，4~20位字母、数字、 _的组合');
return false;
}
if (!this.code) {
this.$vux.toast.text('验证码不能为空');
return false;
}
this.Tools.ajax({
method: '/register',
headers: {
'Content-type': 'application/json'
},
data: {
"username": this.username.trim(),
"telephone": this.telephone.trim(),
"passwd": encrypt.encrypt(this.password),
"code": this.code.trim()
}
}).then(res => {
switch (res.status) {
case '0':

this.$vux.toast.text('注册成功');
setTimeout(() => {
this.$router.goBack();
}, 1500);
break;
case '2':
this.$vux.toast.text('验证码错误');
break;
case '3':
this.$vux.toast.text('验证码过期');
break;
case '4':
this.$vux.toast.text('用户已存在');
break;
default:

this.$vux.toast.text('注册失败，请重试');
break;
}
});

},
clearPhone() {
this.telephone = '';
},
clearNickname() {
this.nickname = '';
},
clearPassword() {
this.password = '';
},
}
}
