
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import encrypt from '../../util/jsencrypt'
export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tabbar,
TabbarItem,
Group,
Cell,
XInput,
XButton
},
data () {
return {
telephone:'15261872169',
password:'87654321',
}
},
methods:{
login(){
if (!this.Tools.RegExp.Phone.test(this.telephone)) {
this.$vux.toast.text('请输入正确的手机号码');
return false;
}
if (!this.password) {
this.$vux.toast.text('请输入密码');
return false;
}

let pwd=encrypt.encrypt(this.password);
this.Tools.ajax({
method: '/appLogin',
data: {
"telephone": this.telephone,
"passwd":pwd
}
}).then(data => {
if (data.status === 1) {
this.$vux.toast.text('账号或密码错误');
return false;
}
localStorage.setItem("tel", this.telephone);
localStorage.setItem("pwd", this.password);
localStorage.setItem("prevToken", data.token);
localStorage.setItem("userCompanyNum", data.num);
localStorage.setItem("userCurrentCompanyStatus", data.companyStatus);






this.$router.push('home');

});
},
register(){
this.$router.push({path: '/register'});
},
clearPhone(){
this.phone='';
},
clearPassword(){
this.password='';
},
}
}
