
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
username: '',
telephone: '17630378060',
password: '',
repassword: '',
code: '',
codeText: '获取验证码',
sending: false,
menus: {
menu1: 'Take Photo',
menu2: 'Choose from photos'
},
showMenus: false
}
},
created(){
this.getAllCompany();
},
methods: {
getAllCompany() {
this.Tools.ajax({
method: '/findByUserId',
data: {
Authorization : localStorage.getItem("prevToken")
}
}).then(res => {
console.log(res)
switch (res.status) {
case '0':

this.$vux.toast.text('验证码已发送请注意查收');
break;
case '2':
this.$vux.toast.text('2分钟内不重复发送短信');
break;
default:

this.$vux.toast.text('发送失败，请稍后重试！');
break;
}
});
},
register() {
console.log(666, this)
if (!this.username) {
this.$vux.toast.text('请输入用户名');
return false;
}
if (!this.Tools.RegExp.Phone.test(this.telephone)) {
this.$vux.toast.text('请输入正确的手机号码');
return false;
}
if (!this.Tools.RegExp.password.test(this.password)) {
this.$vux.toast.text('请输入正确的密码，4~20位字母、数字、 _的组合');
return false;
}
if (this.password !== this.repassword) {
this.$vux.toast.text('两次密码不一致');
return false;
}
if (!this.code) {
this.$vux.toast.text('验证码不能为空');
return false;
}

this.Tools.ajax({
method: '/register',
headers : {
'Content-type': 'application/json'
},
data: {
"username": this.username.trim(),
"telephone": this.telephone.trim(),
"passwd": md5(this.password.trim()),
"code": this.code.trim()
}
}).then(res => {
switch (res.status) {
case '0':

this.$vux.toast.text('注册成功');
setTimeout(() => {
this.$router.goBack();
}, 1500);
break;
case '2':
this.$vux.toast.text('验证码错误');
break;
case '3':
this.$vux.toast.text('验证码过期');
break;
case '4':
this.$vux.toast.text('用户已存在');
break;
default:

this.$vux.toast.text('注册失败，请重试');
break;
}
});

},
clearPhone() {
this.telephone = '';
},
clearPassword() {
this.password = '';
},
}
}
