
import Checklist from 'vux/src/components/checklist/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'


export default {
name: "PopList",
components: {
Checklist,
Checker,
CheckerItem
},
props: {
direction: {
type: Number,
default: () => {
return 1;
}
},
mask: {
type: Object,
default: () => {
return {};
}
},
content: {
type: Object,
default: () => {
return {};
}
},
value: {
type: Boolean,
default: () => {
return false;
}
},
options: {
type: Object,
default: () => {
return {};
}
}
},
data() {
return {
aniName: '',
checklist1: [],
checklist2: []
}
},
created() {
if (this.direction === 1) {
this.aniName = 'contentAni1';
} else if (this.direction === 2) {
this.aniName = 'contentAni2';
} else if (this.direction === 3) {
this.aniName = 'contentAni3';
} else if (this.direction === 4) {
this.aniName = 'contentAni4';
}
if(this.options.type===1){
this.checklist1=this.options.checklist;
}else if(this.options.type===2){
this.checklist2=this.options.checklist;
}
},
watch:{
options(){
if(this.options.type===1){
this.checklist1=this.options.checklist;
}else if(this.options.type===2){
this.checklist2=this.options.checklist;
}
}
},
methods: {
closePop() {
this.$emit('input', false);
},
callback() {
}
}
}
